import { GameStateStore } from 'shared/stores'
import { MessageFactory } from 'shared/factories'
import { Emitter, GameAwardUtils } from 'shared/services'

const STATUSES = {
  UNINITIALIZED: 0,
  INITIALIZED: 1,
  STARTED: 2,
  RESUMED: 3,
  PAUSED: 4,
  STOPPED: 5
}

export default class Game {
  state = null
  status = STATUSES.UNINITIALIZED

  constructor(players, options = { rounds: 8, score: 0 }) {
    this.players = players
    this.options = options
    this.state = new GameStateStore(players, options.rounds, options.score)
    this.status = STATUSES.INITIALIZED
  }

  /**
   * Decide who is the winner when the game is over.
   * @abstract
   * @return {Array<Object>}
   */
  get winners() { return [] }

  start = () => {
    if (this.isInitialized() || this.isStopped()) {
      this.addListeners()
      Emitter.on('CHANGE_BUTTON_CLICK', this.handleChangeButtonClick)
      this.status = STATUSES.STARTED
      this.resume()
    }
  }

  resume = () => {
    if (this.isStarted() || this.isPaused()) {
      Emitter.on('DARTBOARD_HIT', this.handleDartboardHit)
      this.status = STATUSES.RESUMED
    }
  }

  pause = () => {
    if (this.isResumed()) {
      Emitter.off('DARTBOARD_HIT', this.handleDartboardHit)
      this.status = STATUSES.PAUSED
    }
  }

  stop = () => {
    if (this.isStarted() || this.isResumed() || this.isPaused()) {
      Emitter.off('DARTBOARD_HIT', this.handleDartboardHit)
      Emitter.off('CHANGE_BUTTON_CLICK', this.handleChangeButtonClick)
      this.removeListeners()
      this.status = STATUSES.STOPPED
    }
  }

  addListeners = () => {
    Emitter.on('GAME_START', this.start)
    Emitter.on('GAME_RESUME', this.resume)
    Emitter.on('GAME_PAUSE', this.pause)
    Emitter.on('GAME_STOP', this.stop)
  }

  removeListeners = () => {
    Emitter.off('GAME_START', this.start)
    Emitter.off('GAME_RESUME', this.resume)
    Emitter.off('GAME_PAUSE', this.pause)
    Emitter.off('GAME_STOP', this.stop)
  }

  isInitialized = () => this.status === STATUSES.INITIALIZED

  isStarted = () => this.status === STATUSES.STARTED

  isResumed = () => this.status === STATUSES.RESUMED

  isPaused = () => this.status === STATUSES.PAUSED

  isStopped = () => this.status === STATUSES.STOPPED

  /**
   * This'll be triggered after a player throws a dart which hits the dartboard.
   * @abstract
   */
  onDartboardHit = score => {}

  /**
   * This'll be triggered after a player clicks the change button.
   * @abstract
   */
  onChangeButtonClick = () => {}

  /**
   * This'll be triggered when someone wins the game or all players finish their all rounds.
   * @abstract
   */
  onGameOver = () => {}

  handleDartboardHit = scoreSymbol => {
    const { addScoreForCurrentPlayer, currentPlayerScoreboard: scoreboard } = this.state
    const score = addScoreForCurrentPlayer(scoreSymbol)
    if (score) {
      this.state.addMessage(GameAwardUtils.getMessage(score))
      score.index === 2 && this.state.addMessage(GameAwardUtils.getMessage(scoreboard.getCurrentRound()))
      this.onDartboardHit(score)
    }
  }

  handleChangeButtonClick = () => {
    if (!this.isLastRound()) {
      this.onChangeButtonClick()
      this.switchToNextPlayer()
    } else {
      this.handleGameOver()
    }
  }

  switchToNextPlayer = () => {
    const index = (this.state.currentPlayerIndex + 1) % this.players.length 
    this.state.setCurrentPlayerIndex(index)
    this.resume()
  }

  handleGameOver = () => {
    this.addGameMessage('win', this.winners[0].name)
    Emitter.off('CHANGE_BUTTON_CLICK', this.handleChangeButtonClick)
    this.onGameOver()
  }

  isLastRound = () =>
    this.state.currentPlayer === this.state.lastPlayer &&
    this.state.lastPlayerScoreboard.rounds.length === this.options.rounds

  addGameMessage = (id, param) => {
    const message = MessageFactory.create(id, param)
    if (message) {
      message.block && this.pause()
      this.state.addMessage(message)
    }
  }
}

Game.statuses = STATUSES
