const styles = theme => ({
  root: props => ({
    height: '100vh',
    overflowX: 'hidden',
    [theme.breakpoints.between('sm', 'xl')]: {
      overflowY: 'hidden'
    },
    backgroundImage: `url(${props.backgroundImageUrl})`,
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover'
  })
})

export default styles
