import i18n from 'i18n'

const t = i18n.getFixedT()
const MESSAGES = {
  back: { id: 'back', type: 'sound', block: false, soundEffect: 'back' },
  bust: { id: 'bust', type: 'dialog', text: t('ðŸ˜± Bust ðŸ’”'), block: true, soundEffect: 'bust' },
  dart: { id: 'dart', type: 'sound', block: false, soundEffect: 'dart' },
  dbull: bull => ({ id: 'dbull', type: 'snackbar', text: t('bullseyeAward', { bull }), autoHideDuration: 1500, block: false, soundEffect: 'dbull' }),
  gameOn: { id: 'gameOn', type: null, block: false},
  hatTrick: { id: 'hatTrick', type: 'animationDialog', autoHideDuration: 1500, block: false},
  highTon: { id: 'highTon', type: 'animationDialog', autoHideDuration: 1500, block: false },
  lowTon: { id: 'lowTon', type: 'animationDialog', autoHideDuration: 1500, block: false },
  next: { id: 'next', type: 'sound', block: false, soundEffect: 'next' },
  nextPlayer: { id: 'nextPlayer', type: 'dialog', text: t('Press ðŸ”´ to change player'), block: true },
  nextRound: { id: 'nextRound', type: 'dialog', text: t('Press ðŸ”´ to next round'), block: true },
  oneBull: { id: 'oneBull', type: 'animationDialog', autoHideDuration: 1500, block: false },
  sbull: bull => ({ id: 'sbull', type: 'snackbar', text: t('bullseyeAward', { bull }), autoHideDuration: 1500, block: false, soundEffect: 'sbull' }),
  win: name => ({ id: 'win', type: 'dialog', text: t('winnersAward', { name }), block: true, soundEffect: 'win' })
}

export default class MessageFactory {
  static create(id, param) {
    let message = null
    if (MESSAGES[id]) {
      message = typeof MESSAGES[id] === 'function' ? MESSAGES[id](param) : MESSAGES[id]
    }
    return message
  }
}
