import { Howl } from 'howler'
import config from 'config'

const THEMES = ['default']
const SOUND_EFFECTS = [
  'hover', 'press', 'back', 'game_on', 'next',
  'dart', 'sbull', 'dbull', 'bust', 'win'
]

export class SoundEffectService {
  theme = 'default'

  soundEffects = {}

  constructor() {
    this.setTheme(config.soundTheme)
  }

  play = soundEffect =>
    new Promise(resolve => {
      this.soundEffects[soundEffect].on('end', resolve)
      this.soundEffects[soundEffect].play() 
    })

  setTheme = theme => {
    if (THEMES.includes(theme)) {
      this.theme = theme
    }
    this.loadSoundEffects()
  }

  loadSoundEffects = () => {
    SOUND_EFFECTS.forEach(soundEffect => {
      this.soundEffects[soundEffect] = new Howl({ src: [`/resources/sound-effects/${this.theme}/${soundEffect}.webm`] })
      this.soundEffects[soundEffect].on('loaderror', () => {
        this.soundEffects[soundEffect] = new Howl({ src: [`/resources/sound-effects/default/${soundEffect}.webm`] })
      })
    })
  }
}

const soundEffectService = new SoundEffectService()

export default soundEffectService
