import { Game } from 'shared/game'
import { Round } from 'shared/services/Scoreboard'

export default class ZeroOneGame extends Game {
  constructor(players, options = { score: 301, rounds: 15 }) {
    super(players, options)
  }

  get winners() {
    const players = this.state.players.map((player, i) => ({ name: player.name, score: this.state.gameScores[i] }))
                                      .sort((a, b) => a.score - b.score)
    return players.filter(winner => winner.score === players[0].score)
  }

  onDartboardHit = score => {
    const {
      isLastRoundFinished,
      currentPlayerScoreboard: scoreboard,
      decreaseCurrentPlayerGameScore
    } = this.state
    decreaseCurrentPlayerGameScore(score.value)

    const round = scoreboard.getCurrentRound()
    const { currentPlayerGameScore } = this.state
    if (currentPlayerGameScore === 0 || isLastRoundFinished()) {
      this.handleGameOver()
    } else if (currentPlayerGameScore > 0 && round && round.getCurrentScore().index === 2) {
      this.addGameMessage('nextPlayer')
    } else if (currentPlayerGameScore < 0) {
      this.addGameMessage('bust')
    }
  }

  onChangeButtonClick = () => {
    const {
      addRoundForCurrentPlayer,
      clearMessages,
      currentPlayerGameScore,
      deleteCurrentRoundForCurrentPlayer,
      increaseCurrentPlayerGameScore
    } = this.state
    clearMessages()
    if (currentPlayerGameScore < 0) {
      let round = deleteCurrentRoundForCurrentPlayer()
      increaseCurrentPlayerGameScore(round.scores.map(s => s.value).reduce((a, b) => a + b))
      round = new Round(round.index)
      round.addScore('BUST')
      addRoundForCurrentPlayer(round)
    }
    addRoundForCurrentPlayer()
    this.addGameMessage('next')
  }

  handleGameMessageOnClose = message => {
    message.block && this.resume()
    this.state.deleteMessage(message.id)
  }
}
