import { useHistory } from 'react-router-dom'
import { CountUpGameView, ZeroOneGameView } from 'components/games'

export default function GameView(props = { game: {}, players: [] }) {
  const { game, players } = props
  const history = useHistory()
  switch(game.name) {
    case 'zero-one':
      return <ZeroOneGameView players={players} score={game.score} rounds={game.rounds} />
    case 'count-up':
      return <CountUpGameView players={players} rounds={game.rounds} />
    default:
      history.push('/')
      return null
  }
}
