import { useState } from 'react'
import { useHistory } from 'react-router-dom'
import { MenuList, MenuItem } from '@material-ui/core'
import { useTranslation } from 'react-i18next'
import { makeStyles } from '@material-ui/core/styles'
import config from 'config'
import { SoundEffectPlayer } from 'shared/services'
import { MenuItemSelector, Typography } from 'components'
import styles from './styles'

const useStyles = makeStyles(styles)

export default function GameSelectionView(props) {
  const { appState } = props
  const classes = useStyles()
  const { defaultGames: games } = config
  const history = useHistory()
  const [selectedIndex, setSelectedIndex] = useState(0)
  const { t } = useTranslation()

  const handleGameSelect = game => {
    SoundEffectPlayer.play('press')
    appState.game = game
    history.push('/game-menu/players')
  }
  return (
    <MenuList>
      <MenuItemSelector selectedIndex={selectedIndex} setSelectedIndex={setSelectedIndex}>
        {
          games.map((game, i) => (
            <MenuItem key={i} selected={i === selectedIndex} onMouseOver={() => SoundEffectPlayer.play('hover') }
                      onClick={() => handleGameSelect({ name: game.name, rounds: game.rounds, score: game.score }) }>
              <Typography className={classes.game} variant='h2'>{t(game.displayName)}</Typography>
            </MenuItem>
          ))
        }
      </MenuItemSelector>
    </MenuList>
  )
}
