export default class Score {
  valid = true

  constructor(symbol = 'OUT', index = 0, options = { type: 'SOFT_TIP' }) {
    if (!Score.isValid(symbol)) {
      return { valid: false }
    }

    this.symbol = symbol
    this.index = index
    this.options = options

    this.displaySymbol = this.toDisplaySymbol()
    this.value = this.toValue()
  }

  static isValid(symbol) {
    let isValid = false
    if (/^S\d{1,2}I$/.test(symbol) || /^S\d{1,2}O$/.test(symbol)) {
      let digit = parseInt(symbol.slice(1, symbol.length - 1), 10)
      isValid = (digit >= 1 && digit <= 20)
    } else if (/^[DT]\d{1,2}$/.test(symbol)) {
      let digit = parseInt(symbol.slice(1, symbol.length), 10)
      isValid = (digit >= 1 && digit <= 20)
    } else if (/^[SD]BULL$/.test(symbol) || symbol === 'OUT' || symbol === 'BUST') {
      isValid = true
    }
    return isValid
  }

  // TODO: handle the case where dartboard === 'STEEL_TIP' or mode === 'CRICKET'
  toValue() {
    const { symbol } = this
    const calculableSymbol = this.toDisplaySymbol()
    const scoreSymbol = calculableSymbol.slice(1, symbol.length)
    let score
    switch (calculableSymbol.charAt(0)) {
      case 'S':
        score = scoreSymbol === 'BULL' ? 50 : parseInt(scoreSymbol, 10)
        break
      case 'D':
        score = scoreSymbol === 'BULL' ? 50 : parseInt(scoreSymbol, 10) * 2
        break
      case 'T':
        score = parseInt(scoreSymbol, 10) * 3
        break
      case 'O': // for OUT
      case 'B': // for BUST
      default:
        score = 0
    }
    return score
  }

  toDisplaySymbol() {
    const { symbol } = this
    return (symbol.slice(-1) === 'I' || symbol.slice(-1) === 'O') ?
      symbol.slice(0, symbol.length - 1) : symbol
  }
}

