import { OneBull } from 'components/awards'

export default function AnimationDialog(props) {
  const { message, onClose } = props
  switch(message.id) {
    case 'oneBull':
      return <OneBull message={message} onClose={onClose} />
    case 'lowTon':
    case 'hatTrick':
    default:
      return null
  }
}
