import Emitter from 'shared/services/Emitter'

const IMAGES = [
  // The image of Brandenburger Tor is from https://www.pexels.com/photo/concrete-structure-2570063
  { name: 'Brandenburger Tor', url: 'https://images.pexels.com/photos/2570063/pexels-photo-2570063.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260' },
  { name: 'Hallstatt', url: '/resources/images/background/hallstatt.jpg' },
  // The image of Taipei 101 is from https://www.pexels.com/photo/architecture-buildings-business-city-260566
  { name: 'Taipei 101', url: 'https://images.pexels.com/photos/260566/pexels-photo-260566.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260'}
]

export default class BackgroundImageService {
  constructor(setBackgroundImage) {
    this.setBackgroundImage = setBackgroundImage
    this.imageIndex = this.randomIndex
  }

  get randomIndex() {
    return Math.floor(Math.random() * Math.floor(IMAGES.length))
  }

  get randomImage() {
    let index
    do {
      index = this.randomIndex
    } while (index === this.imageIndex)
    this.imageIndex = index
    return IMAGES[index]
  }

  start = () => {
    this.setBackgroundImage(this.randomImage)
    Emitter.on('GAME_MESSAGE', this.handleGameOn)
  }

  stop = () => Emitter.off('GAME_MESSAGE', this.handleGameOn)

  handleGameOn = messageId => messageId === 'gameOn' && this.setBackgroundImage(this.randomImage)
}
