import { useTranslation } from 'react-i18next'
import { MenuList, MenuItem, Typography } from '@material-ui/core'
import config from 'config'
import DartboardFactory from 'shared/factories/DartboardFactory'
import { useHistory } from 'react-router-dom'

const dartboard = DartboardFactory.create(config.dartboard)

export default function GameSelectionView() {
  const { t } = useTranslation()
  const history = useHistory()
  return (
    <MenuList>
      <MenuItem onClick={() => {
        dartboard.connect().then(() => dartboard.connected && history.push('/game-menu'))
      }}>
        <Typography variant='h2'>{t('Connect dartboard')}</Typography>
      </MenuItem>
    </MenuList>
  )
}
