import { SoundEffectPlayer } from 'shared/services'
import Snackbar from '../Snackbar'
import Dialog from '../Dialog'
import AnimationDialog from '../AnimationDialog'

export default function GameMessage(props) {
  const { message, onClose } = props
  switch(message.type) {
    case 'snackbar':
      return <Snackbar message={message} onClose={onClose} />
    case 'dialog':
      return <Dialog message={message} onClose={onClose} />
    case 'animationDialog':
      return <AnimationDialog message={message} onClose={onClose} />
    case 'sound':
    default:
      message.soundEffect && SoundEffectPlayer.play(message.soundEffect)
      onClose && onClose(message)
      return null
  }
}
