import { useTranslation } from 'react-i18next'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import { PlayerCard, Scoreboard, Scores, Typography } from 'components'
import GameScore from './GameScore'
import styles from './styles'

const useStyles = makeStyles(styles)

export default function ClassicDartGameView(props) {
  const { name, player, players, dartScores, scoreboard, gameScore, gameScores } = props
  const { t } = useTranslation()
  const classes = useStyles()
  return (
    <div>
      <div className={classes.gameScore}>
        <GameScore index={player.index} name={player.name} score={gameScore} />
      </div>
      <Typography className={classes.gameName} variant='h4' align='center'>{t(name)}</Typography>
      <Scoreboard className={classes.scoreboard} rounds={scoreboard.rounds} totalRounds={scoreboard.roundsLength} />
      <Scores className={classes.scores} scores={dartScores} />
      <Grid container className={classes.playerCards} spacing={3}>
        {
          players.map((p, i) =>
            <Grid key={i} item xs={3}>
              <PlayerCard index={i} name={p.name} score={gameScores[i]} active={i === player.index} />
            </Grid>
          )
        }
      </Grid>
    </div>
  )
}
