import React from 'react'
import GameMessage from './GameMessage'

export default function GameMessagesView(props) {
  const { messages, onClose } = props
  return (
    <React.Fragment>
      {
        messages.map((message, i) => <GameMessage key={i} message={message} onClose={onClose} />)
      }
    </React.Fragment>
  )
}
