import Emitter from 'shared/services/Emitter'
import Dartboard from '../Dartboard'
import map from './map'

export default class KeyboardDartboard extends Dartboard {
  connection = Dartboard.connections.builtIn

  connected = false

  map = null

  constructor() {
    super()
    this.map = map
  }

  connect = () => {
    document.addEventListener('keypress', this.handleDartboardHit)
    this.connected = true
    return Promise.resolve()
  }

  disconnect = () => {
    document.removeEventListener('keypress', this.handleDartboardHit)
    this.connected = false
    return Promise.resolve()
  }

  handleDartboardHit = event => {
    const { map } = this
    if (event.keyCode >= 32 && event.keyCode <= 114) {
      Emitter.emit('DARTBOARD_HIT', map[event.keyCode])
    } else if (map[event.keyCode] === 'CHANGE') {
      Emitter.emit('CHANGE_BUTTON_CLICK')
    }
  }
}
