const TYPES = {
  steelTip: 'steel-tip',
  softTip: 'soft-tip'
}
const CONNECTIONS = {
  builtIn: 'built-in',
  usb: 'usb',
  bluetooth: 'bluetooth'
}

export default class Dartboard {
  type = TYPES.softTip
  connection = CONNECTIONS.builtIn

  isSteelTipType = () => this.type === TYPES.steelTip

  isSoftTipType = () => this.type === TYPES.softTip

  isBuiltInConnection = () => this.connection === CONNECTIONS.builtIn

  isUsbConnection = () => this.connection === CONNECTIONS.usb

  isBluetoothConnection = () => this.connection === CONNECTIONS.bluetooth
}

Dartboard.types = TYPES
Dartboard.connections = CONNECTIONS
