import { useEffect } from 'react'
import { Dialog as MaterialUiDialog, DialogContent, Typography } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import { SoundEffectPlayer } from 'shared/services'
import styles from './styles'

const useStyles = makeStyles(styles)

export default function Dialog(props) {
  const { message } = props
  const classes = useStyles()
  useEffect(() => {
    message.soundEffect && SoundEffectPlayer.play(message.soundEffect)
  }, [message])
  return (
    <MaterialUiDialog open={true} maxWidth={false} classes={{ scrollPaper: classes.dialogScrollPaper }}>
      <DialogContent>
        <Typography className={classes.text} variant='h1'>{message.text}</Typography>
      </DialogContent>
    </MaterialUiDialog>
  )
}
