import { useEffect, useState } from 'react'
import { GoogleOAuth } from 'shared/services'
import { OAuthContext } from '../OAuthContext'

export default function OAuthProvider(props) {
  const [result, setResult] = useState({ isLogined: false })
  useEffect(() => {
    const oauth = new GoogleOAuth('oauth2-tjdart-params')
    window.oauth = oauth
    oauth.auth().then(result => setResult(result))
  }, [setResult])
  return (
    <OAuthContext.Provider value={result}>
      {props.children}
    </OAuthContext.Provider>
  )
}
