import Score from '../Score'

// ASSUMPTION: always 3 scores per round
// In any real world dart games, always 3 darts for 3 scores per round.
// Maybe we can try to do some changes in the future.
const SCORES_PER_ROUND = 3

export default class Round {
  scores = []

  constructor(index, options) {
    this.index = index
    this.options = options
  }

  addScore = scoreSymbol => {
    try {
      if (this.isNewScoreAddable()) {
        const score = new Score(scoreSymbol, this.scores.length, this.options)
        if (score.valid) {
          this.scores.push(score)
        } else {
          throw new Error('The `scoreSymbol` is invalid!')
        }
        return score
      } else {
        throw new Error('The `scores` array cannot contain more than 3 Score objects!')
      }
    } catch(e) {
      console.error(e)
      return null
    }
  }

  getCurrentScore = () => {
    return this.scores[this.scores.length - 1]
  }

  isNewScoreAddable = () => {
    return this.scores.length < SCORES_PER_ROUND && !this.scores.some(s => s.symbol === 'BUST')
  }
}
