import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import { MenuList, MenuItem } from '@material-ui/core'
import { makeStyles } from '@material-ui/core'
import { useTranslation } from 'react-i18next'
import config from 'config'
import { Emitter, PlayerManager, SoundEffectPlayer } from 'shared/services'
import { MenuItemSelector, Typography } from 'components'
import styles from './styles'

const useStyles = makeStyles(styles)
const playerManager = new PlayerManager(config.players)

export default function PlayerSelectionView(props) {
  const { appState  } = props
  const classes = useStyles()
  const history = useHistory()
  const { players } = playerManager
  const [selectedIndex, setSelectedIndex] = useState(0)
  const { t } = useTranslation()

  const handlePlayersSelect = players => {
    SoundEffectPlayer.play('game_on')
    appState.players = players
    Emitter.emit('GAME_MESSAGE', 'gameOn')
    history.push('/game')
  }
  const handleBackButtonClick = () => {
    SoundEffectPlayer.play('back')
    history.push('/game-menu/games')
  }
  return (
    <MenuList>
      <MenuItemSelector selectedIndex={selectedIndex} setSelectedIndex={setSelectedIndex}>
        {
          [...Array(players.length).keys()].map(i => (
            <MenuItem key={i} selected={i === selectedIndex} onMouseOver={() => SoundEffectPlayer.play('hover') } onClick={() => handlePlayersSelect(players.slice(0, i + 1))}>
              <Typography className={classes.player} variant='h2'>{t(`${i + 1} player${i > 0 ? 's' : ''}`)}</Typography>
            </MenuItem>
          ))
        }
        <MenuItem selected={players.length === selectedIndex} onMouseOver={() => SoundEffectPlayer.play('hover') } onClick={handleBackButtonClick}>
          <Typography variant='h3'>{t('Back')}</Typography>
        </MenuItem>
      </MenuItemSelector>
    </MenuList>
  )
}
