import { useEffect } from 'react'
import { MenuItem } from '@material-ui/core'
import Emitter from 'shared/services/Emitter'

export default function MenuItemSelector(props) {
  const { children, selectedIndex, setSelectedIndex } = props
  useEffect(() => {
    const onMouseOverCallbacks = []
    const onClickCallbacks = []
    const countMenuItemLength = children => {
      let counter = 0
      if (children.type === MenuItem) {
        counter++
        onMouseOverCallbacks.push(children.props.onMouseOver)
        onClickCallbacks.push(children.props.onClick)
      } else if (Array.isArray(children)) {
        children.forEach(child => {
          if (child.type === MenuItem) {
            counter++
            onMouseOverCallbacks.push(child.props.onMouseOver)
            onClickCallbacks.push(child.props.onClick)
          } else if (Array.isArray(child)) {
            counter += countMenuItemLength(child)
          }
        })
      }
      return counter
    }
    const childrenLength = countMenuItemLength(children)

    const handleMenuItemSelect = () => {
      setSelectedIndex((selectedIndex + 1) % childrenLength)
      typeof onMouseOverCallbacks[selectedIndex] === 'function' && onMouseOverCallbacks[selectedIndex]()
    }
    const handleMenuItemSubmit = () => typeof onClickCallbacks[selectedIndex] === 'function' && onClickCallbacks[selectedIndex]()
    Emitter.on('CHANGE_BUTTON_CLICK', handleMenuItemSelect)
    Emitter.on('DARTBOARD_HIT', handleMenuItemSubmit)
    return () => {
      Emitter.off('CHANGE_BUTTON_CLICK', handleMenuItemSelect)
      Emitter.off('DARTBOARD_HIT', handleMenuItemSubmit)
    }
  }, [children, selectedIndex, setSelectedIndex]) // FIXME: Figure out why need to add `selectedIndex` here.
  return children
}
