import Emitter from 'shared/services/Emitter'
import Dartboard from '../Dartboard'

export default class BluetoothDartboard extends Dartboard {
  connection = Dartboard.connections.bluetooth

  valid = true

  map = null

  device = null

  characteristic = null

  constructor(deviceName, serviceUuid, characteristicUuid, options = {}) {
    super()
    if (!navigator.bluetooth) {
      console.warn('Web Bluetooth is not supported.')
      return { valid: false }
    }
    this.deviceName = deviceName
    this.serviceUuid = serviceUuid
    this.characteristicUuid = characteristicUuid
    this.options = { autoReconnect: true, ...options }
    this.map = {}
  }

  get connected() {
    return this.device && this.device.gatt.connected
  }

  connect = () => {
    const { device, deviceName, serviceUuid, characteristicUuid, options } = this
    if (device && device.gatt.connected) {
      return
    }
    return navigator.bluetooth.requestDevice({ filters: [{ name: deviceName }, { services: [serviceUuid] }]})
      .then(device => {
        this.device = device
        options.autoReconnect && device.addEventListener('gattserverdisconnected', this.handleDeviceAutoReconnect)
        return device.gatt.connect()
      })
      .then(server => server.getPrimaryService(serviceUuid))
      .then(service => service.getCharacteristic(characteristicUuid))
      .then(characteristic => {
        this.characteristic = characteristic
        return characteristic.startNotifications()
      })
      .then(() => {
        this.characteristic.addEventListener('characteristicvaluechanged', this.handleDartboardHit)
        return Promise.resolve()
      })
  }

  disconnect = () => {
    const { characteristic, device, options } = this
    if (characteristic) {
      return characteristic.stopNotifications()
        .then(() => characteristic.removeEventListener('characteristicvaluechanged', this.handleDartboardHit))
        .then(() => {
          if (device && device.gatt.connected) {
            options.autoReconnect && device.removeEventListener('gattserverdisconnected', this.handleDeviceAutoReconnect)
            device.gatt.disconnect()
          }
          return Promise.resolve()
        })
    }
    return Promise.resolve()
  }

  handleDartboardHit = e => {
    const { value } = e.target
    let data = []
    for (let i = 0; i < value.byteLength; i++) {
      data.push(('00' + value.getUint8(i).toString(16)).slice(-2))
    }
    const symbol = this.map[parseInt(`0x${data.join('')}`)]
    if (symbol) {
      if (symbol !== 'CHANGE') {
        Emitter.emit('DARTBOARD_HIT', symbol)
      } else {
        Emitter.emit('CHANGE_BUTTON_CLICK')
      }
    }
  }

  handleDeviceAutoReconnect = () => {
    this.device.gatt.connect()
  }
}
