import Round from './Round'
import Score from './Score'

export default class Scoreboard {
  rounds = []

  constructor(roundsLength, options = {}) {
    this.roundsLength = roundsLength
    this.options = options
  }

  addScore = scoreSymbol => {
    let round = this.getCurrentRound()
    if (!(round && round.isNewScoreAddable())) {
      round = this.addRound()
    }
    return round.addScore(scoreSymbol)
  }

  addRound = (round = new Round(this.rounds.length, this.options)) => {
    try {
      if (this.rounds.length < this.roundsLength) {
        this.rounds.push(round)
      } else {
        throw new Error(`The \`rounds\` array cannot contain more than ${this.roundsLength} Round objects!`)
      }
      return round
    } catch(e) {
      console.error(e)
      return null
    }
  }

  getCurrentRound = () => {
    return this.rounds[this.rounds.length - 1]
  }

  removeCurrentRound = () => {
    return this.rounds.pop()
  }

  isLastRoundFinished = () => {
    const round = this.getCurrentRound()
    return (this.rounds.length === this.roundsLength) && (round && !round.isNewScoreAddable())
  }
}

export {
  Scoreboard,
  Round,
  Score
}
