import { action, computed, makeObservable, observable } from 'mobx'
import { Scoreboard } from 'shared/services'

export default class GameStateStore {
  currentPlayerIndex = -1
  currentPlayerDartScores = []
  gameScores = []
  messages = []
  players = []
  scoreboards = []

  constructor(players, rounds, initScore = 0) {
    makeObservable(this, {
      currentPlayerIndex: observable,
      currentPlayerDartScores: observable,
      gameScores: observable,
      messages: observable,
      players: observable,
      scoreboards: observable,
      currentPlayer: computed,
      currentPlayerGameScore: computed,
      currentPlayerScoreboard: computed,
      addRoundForCurrentPlayer: action,
      deleteCurrentRoundForCurrentPlayer: action,
      addMessage: action,
      deleteMessage: action,
      clearMessages: action,
      setCurrentPlayerIndex: action,
      setCurrentPlayerGameScore: action,
      increaseCurrentPlayerGameScore: action,
      decreaseCurrentPlayerGameScore: action
    })
    this.players = players
    players.forEach((player, i) => {
      player.index = i
      this.scoreboards[i] = new Scoreboard(rounds)
      this.scoreboards[i].addRound()
      this.gameScores[i] = initScore
    })
    this.currentPlayerIndex = 0
  }

  get currentPlayer() {
    return this.players[this.currentPlayerIndex]
  }

  get currentPlayerGameScore() {
    return this.gameScores[this.currentPlayerIndex]
  }

  get currentPlayerScoreboard() {
    return this.scoreboards[this.currentPlayerIndex]
  }

  get lastPlayer() {
    return this.players[this.players.length - 1]
  }

  get lastPlayerScoreboard() {
    return this.scoreboards[this.players.length - 1]  
  }

  isLastRoundFinished = () => this.scoreboards[this.players.length - 1].isLastRoundFinished()

  addScoreForCurrentPlayer = scoreSymbol => {
    const score = this.scoreboards[this.currentPlayerIndex].addScore(scoreSymbol)
    if (this.currentPlayerDartScores.length < 3) {
      this.currentPlayerDartScores = this.currentPlayerDartScores.concat(score)
    } else {
      this.currentPlayerDartScores = [score]
    }
    return score
  }

  addRoundForCurrentPlayer = round => {
    const newRound = this.scoreboards[this.currentPlayerIndex].addRound(round)
    if (newRound) {
      this.currentPlayerDartScores = newRound.scores
    }
    return newRound
  }

  deleteCurrentRoundForCurrentPlayer = () => {
    const deletedRound = this.scoreboards[this.currentPlayerIndex].removeCurrentRound()
    const round = this.scoreboards[this.currentPlayerIndex].getCurrentRound()
    if (round) {
      this.currentPlayerDartScores = round.scores
    } else {
      this.currentPlayerDartScores = []
    }
    return deletedRound
  }

  addMessage = message => message && (this.messages = this.messages.concat(message))

  deleteMessage = id => {
    const index = this.messages.findIndex(m => m.id = id)
    index !== -1 && this.messages.splice(index, 1)
  }

  clearMessages = () => this.messages = []

  setCurrentPlayerIndex = index => this.currentPlayerIndex = index

  setCurrentPlayerGameScore = score => this.gameScores[this.currentPlayerIndex] = score

  increaseCurrentPlayerGameScore = score => this.setCurrentPlayerGameScore(this.currentPlayerGameScore + score)

  decreaseCurrentPlayerGameScore = score => this.setCurrentPlayerGameScore(this.currentPlayerGameScore - score)
}
