import { useTranslation } from 'react-i18next'
import { Grid, List, ListItem, ListItemText, Typography } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import styles from './styles'

const useStyles = makeStyles(styles)

export default function Scoreboard(props) {
  const { rounds, totalRounds, className } = props
  const { t } = useTranslation()
  const classes = useStyles()
  const lastEightRounds = rounds.slice(-8)
  return (
    <div className={className}>
      {
        <List>
          <ListItem className={classes.round}>
            <ListItemText primary={
              <Typography className={classes.text} variant='h4'>
                {`${rounds.length} / ${totalRounds} ${t('Rounds')}`}
              </Typography>
            }/>
          </ListItem>
          {
            [...Array(lastEightRounds.length).keys()].map(i => (
              <ListItem key={`round${i}`} className={classes.round}>
                <Grid container>
                  <Grid item xs={2}>
                    <ListItemText primary={
                      <Typography className={classes.text} variant='h6'>
                        {`${(lastEightRounds[i] && (lastEightRounds[i].index + 1)) || i + 1}. `}
                      </Typography>
                    }/>
                  </Grid>
                  {
                    lastEightRounds[i] && lastEightRounds[i].scores.map((score, j) => (
                      <Grid key={`round${i}-score${j}`} item xs={3}>
                        <ListItemText primary={
                          <Typography className={classes.text} variant='h6'>{score.displaySymbol}</Typography>
                        }/>
                      </Grid>
                    ))
                  }
                </Grid>
              </ListItem>
            )).reverse()
          }
        </List>
      }
    </div>
  )
}
