import { useParams } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'
import GameSelectionView from './GameSelectionView'
import PlayerSelectionView from './PlayerSelectionView'
import styles from './styles'

const useStyles = makeStyles(styles)

export default function GameMenuView(props) {
  const { appState } = props
  const classes = useStyles()
  const { step } = useParams()
  const ActiveView = props => {
    switch(props.step) {
      case 'games':
        return <GameSelectionView appState={appState} />
      case 'players':
        return <PlayerSelectionView appState={appState} />
      default:
        return null
    }
  }
  return <div className={classes.root}><ActiveView step={step} /></div>
}
