import { useEffect, useState } from 'react'
import { Snackbar as MaterialUiSnackbar, Typography } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import { SoundEffectPlayer } from 'shared/services'
import styles from './styles'

const useStyles = makeStyles(styles)

export default function Snackbar(props) {
  const { message, onClose } = props
  const classes = useStyles()
  const [open, setOpen] = useState(true)
  useEffect(() => {
    setOpen(true)
    message.soundEffect && SoundEffectPlayer.play(message.soundEffect)
  }, [message])
  return (
    <MaterialUiSnackbar
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'center'
      }}
      className={classes.snackbar}
      open={open}
      autoHideDuration={message.autoHideDuration}
      onClose={() => {
        setOpen(false)
        onClose && onClose(message)
      }}
      message={<Typography className={classes.text} variant='h1'>{message.text}</Typography>}
      ContentProps={{ className: classes.snackbarContent }}
    />
  )
}
