import { UserStore } from 'shared/stores'

const LOGIN_SUCCESS_MESSAGE = { isLogined: true }
const LOGIN_FAILURE_MESSAGE = { isLogined: false }

export default class GoogleOAuth {
  constructor(
    storageItemName,
    clientId = process.env.REACT_APP_GOOLGE_CLIENT_ID,
    redirectUri = process.env.REACT_APP_HOST
  ) {
    this.storageItemName = storageItemName
    this.clientId = clientId
    this.redirectUri = redirectUri
  }

  auth = () => {
    return this.isLogined().then(result => {
      if (!result.isLogined) {
        let params = {}
        const searchParams = new URLSearchParams(window.location.hash.substring(1))
        for (const [key, value] of searchParams) {
          params[key] = value
        }
        if (Object.keys(params).length > 0) {
          localStorage.setItem(this.storageItemName, JSON.stringify(params))
          if (params['state'] && params['state'] === 'try_sample_request') {
            return this.isLogined()
          }
        } else {
          this.openSignInForm()
        }
      }
      return Promise.resolve(result)
    })
  }

  isLogined = () =>
    new Promise(resolve => {
      const params = JSON.parse(localStorage.getItem(this.storageItemName))
      if (params && params['access_token']) {
        const xhr = new XMLHttpRequest()
        xhr.open('GET', `https://people.googleapis.com/v1/people/me?personFields=emailAddresses&access_token=${params['access_token']}`)
        xhr.onreadystatechange = () => {
          try {
            if (xhr.readyState === 4) {
              if (xhr.status === 200) {
                const email = JSON.parse(xhr.response).emailAddresses[0].value
                if (UserStore.has(email)) {
                  resolve(LOGIN_SUCCESS_MESSAGE)
                } else {
                  resolve(LOGIN_FAILURE_MESSAGE)
                }
              } else {
                resolve(LOGIN_FAILURE_MESSAGE)
              }
            }
          } catch (e) {
            console.error(e)
            resolve(LOGIN_FAILURE_MESSAGE)
          }
        }
        xhr.send(null)
      } else {
        resolve(LOGIN_FAILURE_MESSAGE)
      }
    })

  openSignInForm = () => {
    const form = document.createElement('form')
    form.setAttribute('method', 'GET')
    form.setAttribute('action', 'https://accounts.google.com/o/oauth2/v2/auth')
    const params = {
      'client_id': this.clientId,
      'redirect_uri': this.redirectUri,
      'scope': 'profile https://www.googleapis.com/auth/user.emails.read',
      'state': 'try_sample_request',
      'response_type': 'token'
    }
    for (let param in params) {
      const input = document.createElement('input')
      input.setAttribute('type', 'hidden')
      input.setAttribute('name', param)
      input.setAttribute('value', params[param])
      form.appendChild(input)
    }
    document.body.appendChild(form)
    form.submit()
  }
}
