import { MessageFactory } from 'shared/factories'
import { Round, Score } from 'shared/services/Scoreboard'

export default class GameAwardUtils {
  static isBullseye(score) {
    return (score.symbol === 'SBULL' || score.symbol === 'DBULL')
  }

  static getMessage(darts) {
    let message = null
    if (darts instanceof Score) {
      if (GameAwardUtils.isBullseye(darts)) {
        const symbol = darts.symbol.toLowerCase()
        message = MessageFactory.create(symbol, symbol)
      } else {
        message = MessageFactory.create('dart')
      }
    } else if (darts instanceof Round) {
      const { scores } = darts
      const sum = sumScores(scores)
      const bullCounter = countBullScores(scores)
      if (sum < 100 && bullCounter === 1) {
        message = MessageFactory.create('oneBull')
      } else if (sum >= 100 && sum < 150) {
        message = MessageFactory.create('lowTon')
      } else if (bullCounter === 3) {
        message = MessageFactory.create('hatTrick')
      } else if (sum >= 151) {
        message = MessageFactory.create('highTon')
      }
    }
    return message
  }
}

const countBullScores = scores => {
  let counter = 0
  scores.forEach(score => (score.symbol === 'SBULL' || score.symbol === 'DBULL') && counter++)
  return counter
}

const sumScores = scores => {
  let sum = 0
  scores.forEach(score => sum += score.toValue())
  return sum
}
